<template>
  <div class="waveWrapper waveAnimation">

    <div class="bck">
      <ConversationalForm @submit="submit" class="position">
        <fieldset>
          <label for="email" >!   ?</label>
          <input pattern="^[a-zA-Z--]+$"
                 data-invalid="  ."
                 data-question="!   ? "
                 type="text"
                 name="email"
                 id="name">
        </fieldset>
        <fieldset>
          <label for="surname">{email},   ?</label>
          <input required
                 pattern="^[a-zA-Z--]+$"
                 data-invalid="  ."
                 data-question="{email},   ? "
                 type="text" name="surname" id="surname">
        </fieldset>
        <fieldset>
          <label for="name"></label><br />
          <input type="radio" data-question="{email} {surname},   ?" name="gender" value="male" data-text="" /> <br>
          <input type="radio" name="gender" value="female" data-text="" /> <br>
          <input type="radio" name="gender" value="other" data-text=" " />  
        </fieldset>

        <fieldset>
          <label for="age">{email} {surname},    ?</label>
          <input required
                 pattern="[0-9]{2}"
                 data-invalid="  ."
                 data-question="{email} {surname},    ? "
                 type="number" name="age" id="age">
        </fieldset>

        <fieldset>
          <label for="opinion">{email} {surname},    ?</label>
          <select data-question="{email} {surname},    ?" name="opinion" id="opinion">
            <option></option>
            <option>HR</option>
            <option>Team Lead</option>
            <option></option>
          </select>
        </fieldset>
        <fieldset>
          <label for="company">Company</label>
          <input data-question="{opinion}, !     ?" type="text" name="company" id="company">
        </fieldset>
        <fieldset>
          <label >{email} {surname},    ?</label>
          <button data-question="{email} {surname},    ?" data-success="! ! " name="submit" type="submit" data-cancel=""></button>
        </fieldset>
        <fieldset>
          <label >   ?</label>
          <button data-question="   ?" name="reset" type="reset" data-cancel=""></button>
        </fieldset>
      </ConversationalForm>
    </div>

    <div class="waveWrapperInner bgTop">
      <div class="wave waveTop" style="background-image: url('http://front-end-noobs.com/jecko/img/wave-top.png')"></div>
    </div>
    <div class="waveWrapperInner bgMiddle">
      <div class="wave waveMiddle" style="background-image: url('http://front-end-noobs.com/jecko/img/wave-mid.png')"></div>
    </div>
    <div class="waveWrapperInner bgBottom">
      <div class="wave waveBottom" style="background-image: url('http://front-end-noobs.com/jecko/img/wave-bot.png')"></div>
    </div>
  </div>

</template>

<script>
  import ConversationalForm from 'vue-conversational-form'
  export default {
    name: "Profile",
    components: {
      ConversationalForm
    },
    methods: {
      submit (profile) {
        localStorage.setItem('userProfile', 'yes');
        this.$store.dispatch('createProfile', profile);
        setTimeout(function(){
          location.reload()
        }, 1800);
      }
    }
  }
</script>

<style scoped lang="sass">
  .bck
    z-index: 50
    width: 100vw
    height: calc(100vh - 64px)
    margin-top: 50px
    display: flex
    align-items: center
    justify-content: center
    position: absolute

  .position
    position: absolute
    top: 50%
    left: 50%
    transform: translate(-50%,-50%)

</style>
